import axios from "axios";
import { csvParse } from "d3-dsv";
import { PRIORITY, PriorityQueue } from "../utils";

var TIFF_MAX_REQUEST = 5;
var TIFF_TIMEOUT = 20;

var tiffRequest = [];
var pqTiff = new PriorityQueue();

// used to set state (only for simple assignment)
// it is requried to know field name
export function setValue(name, value) {
    return {
        type: "SET_VALUE",
        payload: { name: name, value: value }
    };
}

// used one time from appIndex.js
// todo: can move into appIndex.js as local function
export function get_color_map() {
    return function (dispatch) {
        axios.get("/static/resources/data/cm/Cool.csv").then(function (response) {
            return response.data;
        }).then(function (data) {
            return csvParse(data, function (d) {
                return {
                    r: +d.Red,
                    g: +d.Green,
                    b: +d.Blue
                };
            });
        }).then(function (data) {
            dispatch({
                type: "GET_COLORMAP",
                payload: data
            });
        }).catch(function (e) {
            dispatch({
                type: "GET_COLORMAP_REJECTED",
                payload: e
            });
        });
    };
}

