// used to get data by sample names
export function get_data(sampleNames, project) {
    return function (dispatch) {
        axios.post("/api/data/sample", { sampleNames: sampleNames, project: project }).then(function (resp) {
            dispatch({
                type: "GET_DATA",
                payload: resp.data
            });
        }).catch(function (e) {
            dispatch({
                type: "GET_DATA_REJECTED",
                payload: e
            });
        });
    };
}

