// used to handle image data request
var imageRequestOnProgress = function imageRequestOnProgress() {
    var count = tiffRequest.length + pqTiff.length();
    return count;
};

function get_tiff(key) {
    return function (dispatch) {
        var tokens = key.split("::");
        var id = tokens[0];
        var db = tokens[1];
        var col = tokens[2];
        axios.post("/api/data/tiff", { id: id, db: db, col: col }).then(function (resp) {
            var idx = tiffRequest.indexOf(key);
            if (idx > -1) tiffRequest.splice(idx, 1);
            dispatch({
                type: "GET_TIFF",
                payload: {
                    id: id,
                    data: resp.data,
                    count: imageRequestOnProgress()
                }
            });
        }).catch(function (e) {
            dispatch({
                type: "GET_TIFF_REJECTED",
                payload: e
            });
        });
    };
}

export function get_tiff_with_priority(id, project) {
    var priority = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : PRIORITY.LOW_MID;

    return function (dispatch) {
        var db = project['db'];
        var col = project['col'];
        var key = id + "::" + db + "::" + col;
        if (tiffRequest.indexOf(key) > -1) return;

        pqTiff.replace(key, priority, function (a, b) {
            return a === b;
        });
        if (tiffRequest.length > TIFF_MAX_REQUEST) return;

        var pended = pqTiff.front();
        tiffRequest.push(pended.data);

        setTimeout(function () {
            dispatch(get_tiff(pended.data));
        }, TIFF_TIMEOUT * tiffRequest.length / pended.priority);
    };
}

